<template><section><html><head></head><body><h1>PlaneGraphics</h1>
<p>The <code v-pre="">vc-graphics-plane</code> component is used to load a plane. Need to be used as a subcomponent of <code v-pre="">vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load PlaneGraphics with <code v-pre="">vc-graphics-plane</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :position="position1" :description="description" :plane.sync="planeEntity1">
          <vc-graphics-plane ref="plane1" :plane="plane1" :dimensions="dimensions1" :material="material1"></vc-graphics-plane>
        </vc-entity>
        <vc-entity :position="position2" :description="description" :plane.sync="planeEntity2">
          <vc-graphics-plane ref="plane2" :plane="plane2" :dimensions="dimensions2" :material="material2" :outline="true" :outline-color="outlineColor2"></vc-graphics-plane>
        </vc-entity>
        <vc-entity :position="position3" :description="description" :plane.sync="planeEntity3">
          <vc-graphics-plane ref="plane3" :plane="plane3" :dimensions="dimensions3" :fill="false" :outline="true" :outline-color="outlineColor3"></vc-graphics-plane>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
