        <span class="hljs-attr">dimensions2</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">400000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">material2</span>: {},
        <span class="hljs-attr">outlineColor2</span>: <span class="hljs-string">'BLACK'</span>,

        <span class="hljs-attr">position3</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">100.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">planeEntity3</span>: {},
        <span class="hljs-attr">plane3</span>: { <span class="hljs-attr">normal</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">z</span>: <span class="hljs-number">1</span> }, <span class="hljs-attr">distance</span>: <span class="hljs-number">0.0</span> },
        <span class="hljs-attr">dimensions3</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">400000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">outlineColor3</span>: <span class="hljs-string">'YELLOW'</span>
      }
    },
    <span class="hljs-title function_">mounted</span>(<span class="hljs-params"></span>) {
      <span class="hljs-title class_">Promise</span>.<span class="hljs-title function_">all</span>([<span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">plane1</span>.<span class="hljs-property">createPromise</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">plane2</span>.<span class="hljs-property">createPromise</span>, <span class="hljs-variable language_">this</span>.<span class="hljs-property">$refs</span>.<span class="hljs-property">plane3</span>.<span class="hljs-property">createPromise</span>]).<span class="hljs-title function_">then</span>(
