<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying the visibility of the plane.</td>
</tr>
<tr>
<td>plane</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Plane Property specifying the normal and distance for the plane. <strong>structure: { normal: { x: number, y: number, z: number }, distance: number }</strong></td>
</tr>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Cartesian2 Property specifying the width and height of the plane. <strong>structure: { x: number, y: number }</strong></td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the plane is filled with the provided material.</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre="">'WHITE'</code></td>
<td><code v-pre="">optional</code> A Property specifying the material used to fill the plane.</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> A boolean Property specifying whether the plane is outlined.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">'BLACK'</code></td>
<td><code v-pre="">optional</code> A Property specifying the Color of the outline.</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> A numeric Property specifying the width of the outline.</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> An enum Property specifying whether the plane casts or receives shadows from each light source. <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> A Property specifying at what distance from the camera that this plane will be displayed. <strong>structure: { near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PlaneGraphics.html">PlaneGraphics</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>Triggers whenever a property or sub-property is changed or modified.</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          position1: { lng: 114.0, lat: 40.0, height: 300000.0 },
          planeEntity1: {},
          plane1: { normal: { x: 1, y: 0, z: 0 }, distance: 0.0 },
          dimensions1: { x: 400000.0, y: 300000.0 },
          material1: 'BLUE',

          position2: { lng: 107.0, lat: 40.0, height: 300000.0 },
          planeEntity2: {},
          plane2: { normal: { x: 0, y: 1, z: 0 }, distance: 0.0 },
          dimensions2: { x: 400000.0, y: 300000.0 },
          material2: {},
          outlineColor2: 'BLACK',

          position3: { lng: 100.0, lat: 40.0, height: 300000.0 },
          planeEntity3: {},
          plane3: { normal: { x: 0, y: 0, z: 1 }, distance: 0.0 },
          dimensions3: { x: 400000.0, y: 300000.0 },
          outlineColor3: 'YELLOW'
        }
      },
      mounted() {
        Promise.all([this.$refs.plane1.createPromise, this.$refs.plane2.createPromise, this.$refs.plane3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material2 = Cesium.Color.RED.withAlpha(0.5)
        }
      }
    }
  </script>